import Nails from '../Nails.js';

const PI2 = Math.PI * 2;

export default class Circle {
  constructor(config) {
    this.setConfig(config);
  }

  getPoint(index = 0) {
    if (this.points.has(index)) {
      return this.points.get(index);
    }

    const realIndex = this.isReverse ? this.config.n - 1 - index : index;

    const point = [
      this.center[0] +
        Math.sin(realIndex * this.indexAngle + this.rotationAngle) *
          this.radius,
      this.center[1] +
        Math.cos(realIndex * this.indexAngle + this.rotationAngle) *
          this.radius,
    ];

    this.points.set(index, point);
    return point;
  }

  setConfig(config) {
    const serializedConfig = this._serializeConfig(config);
    if (serializedConfig !== this.serializedConfig) {
      const {
        n,
        size,
        margin = 0,
        rotation = 0,
        center: configCenter,
        radius,
        reverse = false,
      } = config;
      const center = configCenter ?? size.map(v => v / 2);
      const props = {
        center,
        radius: radius ?? Math.min(...center) - margin,
        indexAngle: PI2 / n,
        rotationAngle: -PI2 * rotation,
        isReverse: reverse,
      };
      this.config = config;
      this.serializedConfig = serializedConfig;
      Object.assign(this, props);
      if (this.points) {
        this.points.clear();
      } else {
        this.points = new Map();
      }
    }
  }

  _serializeConfig({
    n,
    size,
    margin = 0,
    rotation = 0,
    center,
    radius,
    reverse = false,
  }) {
    return [
      size?.join(','),
      center?.join(','),
      radius,
      margin,
      n,
      rotation,
      reverse,
    ].join('_');
  }

  /**
   * Given a Nails instance, uses it to draw the nails of this Circle
   * @param {Nails} nails
   * @param {{nailsNumberStart?: number, getNumber?: Function}} param1
   */
  drawNails(nails, { nailsNumberStart = 0, getNumber } = {}) {
    for (let i = 0; i < this.config.n; i++) {
      nails.addNail({
        point: this.getPoint(i),
        number: getNumber ? getNumber(i) : i + nailsNumberStart,
      });
    }
  }

  static rotationConfig = Object.freeze({
    key: 'rotation',
    label: 'Rotation',
    defaultValue: 0,
    type: 'range',
    attr: {
      min: 0,
      max: 1 + 1 / 360,
      step: 1 / 360,
    },
    displayValue: (config, { key }) => `${Math.round(config[key] * 360)}`,
  });

  static nailsConfig = Object.freeze({
    key: 'n',
    label: 'Number of nails',
    defaultValue: 144,
    type: 'range',
    attr: {
      min: 3,
      max: 300,
      step: 1,
    },
  });
}
